// pages / dashboard / payment-history / view-payment

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class PaymentHistoryViewPayment extends React.Component {
    printPayment() {
        window.print();
    }

    render() {
        if (this.props.user.loggedIn) {

            return (
                  <span>

                      {/* Start of Helmet */}
                      <Helmet title="Payment History - view" meta={[
                          {
                              name: 'description',
                              content: 'Payment History - view'
                          }, {
                              name: 'keywords',
                              content: 'Payment History'
                          }
                      ]}/>
                      {/* End of Helmet */}


                      {/* Start of Heading */}
                      <div className="row wrapper border-bottom white-bg page-heading" id="print-disable">
                          <div className="col-lg-8">
                              <h2>Payment</h2>
                              <ol className="breadcrumb">
                                  <li>
                                      <Link to="/dashboard">Home</Link>
                                  </li>
                                  <li>
                                      <Link to="/dashboard/payment-history">Payment History List</Link>
                                  </li>
                                  <li className="active">
                                      <strong>Payment Detail</strong>
                                  </li>
                              </ol>
                          </div>
                          <div className="col-lg-4">
                              <div className="title-action">
                                  <a href="#" className="btn btn-white"><i className="fa fa-pencil"></i> Edit </a>
                                  <a href="#" className="btn btn-white"><i className="fa fa-check "></i> Save </a>
                                  <a href="#" target="_blank" className="btn btn-primary" onClick={this.printPayment.bind(this)}>
                                      <i className="fa fa-print"></i> Print Payment
                                  </a>
                              </div>
                          </div>
                      </div>
                      {/* End of Heading */}


                      {/* Start of Content */}
                      <div className="wrapper wrapper-content animated fadeInRight" id="print-enable">
                          <div className="ibox-content p-xl">
                              <div className="row">
                                  <div className="col-sm-6">
                                      <h5>From:</h5>
                                      <address>
                                          <strong>Tenaga Nasional Berhad</strong><br/>
                                          No 129, Jalan Bangsar 59200 Kuala Lumpur<br/>
                                        <abbr title="Tel">Tel : </abbr> +603 2296 5566<br/>
                                          <abbr title="Fax">Fax : </abbr> +603 2283 3686
                                      </address>
                                  </div>

                                  <div className="col-sm-6 text-right">
                                      <h4>Payment No.</h4>
                                      <h4 className="text-navy">INV-1234567-12</h4>
                                      <span>To:</span>
                                      <address>
                                          <strong>Devv Thayveen</strong><br/>
                                          1, Lebuh Bandar Utama,<br/>
                                          47800, Petaling Jaya, Selangor<br/>
                                          Malaysia<br/>
                                          <abbr title="Tel">Tel:</abbr> +60-312345678<br/>
                                          <abbr title="Email">Email:</abbr> example@example.com
                                      </address>
                                      <p>
                                          <span><strong>Bill Type:</strong> Electric Bill</span><br/>
                                          <span><strong>Due Date:</strong> March 24, 2017</span><br/>
                                          <span><strong>Payment Date:</strong> March 18, 2017</span>
                                      </p>
                                  </div>
                              </div>

                              <div className="table-responsive m-t">
                                  <table className="table invoice-table">
                                      <thead>
                                          <tr>
                                              <th>Item List</th>
                                              <th>Quantity</th>
                                              <th>Unit Price</th>
                                              <th>Tax</th>
                                              <th>Total Price</th>
                                          </tr>
                                      </thead>
                                      <tbody>
                                          <tr>
                                              <td>
                                                  <div><strong>Late payment charge (January) for electric bill</strong></div>
                                                  <small>394 kWh Energy Consumption</small>
                                              </td>
                                              <td>1</td>
                                              <td>RM 26.00</td>
                                              <td>RM 5.98</td>
                                              <td>RM 31,98</td>
                                          </tr>
                                          <tr>
                                              <td>
                                                  <div><strong>Late payment charge (February) for electric bill</strong></div>
                                                  <small>
                                                      394 kWh Energy Consumption
                                                  </small>
                                              </td>
                                              <td>2</td>
                                              <td>RM 80.00</td>
                                              <td>RM 36.80</td>
                                              <td>RM 196.80</td>
                                          </tr>
                                          <tr>
                                              <td>
                                                  <div><strong>Payment charge (March) for electric bill</strong></div>
                                                  <small>
                                                      394 kWh Energy Consumption
                                                  </small>
                                              </td>
                                              <td>3</td>
                                              <td>RM 420.00</td>
                                              <td>RM 193.20</td>
                                              <td>RM 1033.20</td>
                                          </tr>

                                      </tbody>
                                  </table>
                              </div>
                              <table className="table invoice-total">
                                  <tbody>
                                      <tr>
                                          <td><strong>Sub Total :</strong></td>
                                          <td>RM 1026.00</td>
                                      </tr>
                                      <tr>
                                          <td><strong>TAX :</strong></td>
                                          <td>RM 235.98</td>
                                      </tr>
                                      <tr>
                                          <td><strong>TOTAL :</strong></td>
                                          <td>RM 1261.98</td>
                                      </tr>
                                  </tbody>
                              </table>
                              <div className="space-25"></div>
                              <div className="text-right">
                                  <button id="print-disable" className="btn btn-primary" onClick={this.printPayment.bind(this)}>
                                      <i className="fa fa-print"></i> Print Payment
                                  </button>
                              </div>

                              <div className="well m-t">
                                  <strong>Comments: </strong> Thank you for your payment, please contact us if any support is needed.
                              </div>
                          </div>
                          <div className="space-25"></div>
                      </div>
                      {/* End of Content */}

                  </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(PaymentHistoryViewPayment);



